/* custom checkbox and radio inputs css */
.wb_control {
  display: block;
  position: relative;
  cursor: pointer;
  font-size: 16px;
  color: #162234; }

.wb_control input {
  position: absolute;
  z-index: -1;
  opacity: 0; }

.wb_control__indicator {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  margin-right: 10px;
  height: 24px;
  width: 24px;
  transition: all 0.3s ease-in 0s;
  background: #F1F3F5;
  border: 1px solid #DEE2E6; }

.wb_radio .wb_control__indicator {
  border-radius: 50%; }

/*.wb_control input:focus~.wb_control__indicator,
.wb_control:hover input~.wb_control__indicator {
	background: #ccc
}*/
.wb_control input:checked ~ .wb_control__indicator {
  background: #fff;
  border-color: #077AEF; }

/*.wb_control input:checked:focus~.wb_control__indicator,
.wb_control:hover input:not([disabled]):checked~.wb_control__indicator {
	background: #0e647d
}
*/
.wb_control input:disabled ~ .wb_control__indicator {
  background: #e6e6e6;
  opacity: .6;
  pointer-events: none; }

.wb_control__indicator:after {
  content: '';
  position: absolute;
  display: none; }

.wb_control input:checked ~ .wb_control__indicator:after {
  display: block; }

.wb_checkbox .wb_control__indicator:after {
  left: 8px;
  top: 4px;
  width: 7px;
  height: 12px;
  border: solid #fff;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg); }

.wb_checkbox input:disabled ~ .wb_control__indicator:after {
  border-color: #7b7b7b; }

.wb_radio .wb_control__indicator:after {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  height: 14px;
  width: 14px;
  border-radius: 50%;
  background: #077AEF; }

.wb_radio input:disabled ~ .wb_control__indicator:after {
  background: #fff; }

.wb_checkbox .wb_control__indicator {
  border: 1px solid #DEE2E6;
  border-radius: 4px;
  margin-right: 8px; }

.wb_checkbox input:checked ~ .wb_control__indicator {
  background: #077AEF;
  border-color: #077AEF; }

